.header {
    padding: 45px 0 41px;
    border-bottom: solid rgba(84, 84, 84, 0.7) 1px;
    display: flex;
    justify-content: space-between;
    max-width: 100%;
}

@media screen and (max-width:880px) {
    .header {
        margin: 0 19px 0;
    }
}

@media screen and (max-width:696px) {
    .header {
        padding: 30px 0;
        margin: 0;
    }
}